import React from "react";
import myData from "../../data/my-data";
import EducationCard from "./components/EducationCard";

const Education = () => {
    return (
        <div>
            <h1 className="theme-font theme-color">Education</h1>
            <br />
            <div id="edu-div">
                {myData.education.map((record, index) => (
                    <div key={index}>
                        <EducationCard record={record} />
                        {index !== myData.education.length - 1 && <br />}
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Education;
