import React from "react";

const alignCenter = {
    textAlign: "center",
};

const details = {
    fontSize: "1.3rem",
};

const EducationCard = (props) => {
    return (
        <div className="education-card">
            <div className="row">
                <div className="col-lg-2" style={alignCenter}>
                    <img
                        className="education-img"
                        src={props.record.imgSrc}
                        alt={props.record.alt}
                    />
                </div>
                <div className="col-lg-10">
                    <h3>
                        {props.record.degree}
                        <span className="year-font">
                            <strong>
                                <em>{props.record.period}</em>
                            </strong>
                        </span>
                    </h3>
                    <div className="theme-text">
                        <p>
                            <a
                                target="blank"
                                href={props.record.src}
                                className="hyp-link"
                            >
                                {props.record.school}
                            </a>
                        </p>
                        <p style={details}>
                            <span
                                dangerouslySetInnerHTML={{
                                    __html: props.record.details,
                                }}
                            ></span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EducationCard;
