import React from "react";
import myData from "../data/my-data";
import BlogCard from "./blogs/BlogCard";

const Blogs = () => {
    return (
        <div>
            <br />
            <h1 className="theme-font theme-color">Blogs</h1>
            <div className="row">
                {myData.blogs.map((blog, index) => (
                    <BlogCard
                        key={index}
                        title={blog.title}
                        src={blog.src}
                        img={blog.img}
                    />
                ))}
            </div>
        </div>
    );
};

export default Blogs;
