import React from "react";
import { useState } from "react";
import AboutButton from "./about/components/AboutButton";
import myData from "../data/my-data";
import AboutMe from "./about/AboutMe";
import Education from "./about/Education";
import TechnicalSkills from "./about/TechnicalSkills";
import GitHubProfile from "./about/GitHubProfile";

const alignCenter = {
    textAlign: "center",
};

const About = () => {
    const [active, setActive] = useState("about-me");

    function changeContent(event) {
        let name = event.currentTarget.name;
        setActive(name);
    }

    return (
        <section>
            <div style={alignCenter}>
                {myData.abouts.map((about) => {
                    return (
                        <AboutButton
                            key={about.id}
                            class={about.icon}
                            title={about.title}
                            id={about.id}
                            onClick={changeContent}
                            active={active}
                        />
                    );
                })}
            </div>

            {active === "about-me" && <AboutMe />}
            {active === "education" && <Education />}
            {active === "tech-skills" && <TechnicalSkills />}
            {active === "github-profile" && <GitHubProfile />}
        </section>
    );
};

export default About;
