import React from "react";
import ContactIcon from "./ContactIcon";

const Contacts = (props) => {
    return (
        <div id="contact">
            {props.data.map((contact) => {
                return (
                    <ContactIcon
                        key={contact.id}
                        src={contact.link}
                        fa={contact.icon}
                    />
                );
            })}
        </div>
    );
};

export default Contacts;