import React from "react";

const GitHubStats = (props) => {
    let link = `https://github-readme-stats.vercel.app/api?username=${props.user}&show_icons=true&theme=dark&hide_border=true&icon_color=fdb328`;
    return (
        <div>
            <p className="theme-text">Stats</p>
            <embed className="github-stats" src={link} />
            <br />
        </div>
    );
};

export default GitHubStats;
