import React from "react";

const cursor = { cursor: "pointer" };
const cls = "nav-link";

const NavLink = (props) => (
    <li className="nav-item">
        <div
            style={cursor}
            className={props.active === props.id ? cls + " active" : cls}
            onClick={() => {
                props.onClick(props.id);
            }}
        >
            {props.text}
        </div>
    </li>
);

export default NavLink;
