import React from "react";

const RepositoryCard = (props) => {
    let link = `https://github-readme-stats.vercel.app/api/pin/?username=${props.user}&repo=${props.repo}&theme=dark&icon_color=fdb328&show_owner=true&hide_border=true`;
    return <embed className="github-repos" src={link} />;
};

export default RepositoryCard;
